class QuizType extends Phaser.Sprite {

    constructor(game, x, y, key, context) {
      
        super(game, x, y, key);
        // width, height
        
        // default
        window.config = window.configAdd;
        
				this.xposOrig = 26;
        this.xpos = this.xposOrig;
				this.yposOrig = 210;
        this.ypos = this.yposOrig;
        
        var text = null;
        
        // Add
        var button11 = game.add.button(this.xpos, this.ypos, key, function() { window.config=window.configAdd }, context, 0,1,2,3);
    		button11.anchor.x = 0.5; 
        button11.anchor.y = 0.5;
        button11.scale.setTo(0.14,0.14);
    		text = game.add.text(-60, -100, "+", {font: "200px Arial", fill: "#000000"});
    		button11.addChild(text);
        this.xpos+=38;
        
        // Del
        var button12 = game.add.button(this.xpos, this.ypos, key, function() { window.config=window.configDel }, context, 0,1,2,3);
    		button12.anchor.x = 0.5; 
        button12.anchor.y = 0.5;
        button12.scale.setTo(0.14,0.14);
    		text = game.add.text(-30, -120, "-", {font: "200px Arial", fill: "#000000"});
    		button12.addChild(text);
        this.xpos+=38;
        
        // Mul
        var button13 = game.add.button(this.xpos, this.ypos, key, function() { window.config=window.configMul }, context, 0,1,2,3);
    		button13.anchor.x = 0.5; 
        button13.anchor.y = 0.5;
        button13.scale.setTo(0.14,0.14);
    		text = game.add.text(-50, -120, "x", {font: "200px Arial", fill: "#000000"});
    		button13.addChild(text);
        this.xpos+=38;
        
        // Mul
        var button14 = game.add.button(this.xpos, this.ypos, key, function() { window.config=window.configRhy }, context, 0,1,2,3);
    		button14.anchor.x = 0.5; 
        button14.anchor.y = 0.5;
        button14.scale.setTo(0.14,0.14);
    		text = game.add.text(-40, -120, "r", {font: "200px Arial", fill: "#000000"});
    		button14.addChild(text);
				this.xpos = this.xposOrig;
        this.ypos+=40;
        
				// Tmp
        var button15 = game.add.button(this.xpos, this.ypos, key, function() { window.config=window.configImg }, context, 0,1,2,3);
    		button15.anchor.x = 0.5; 
        button15.anchor.y = 0.5;
        button15.scale.setTo(0.14,0.14);
    		text = game.add.text(-30, -120, "i", {font: "200px Arial", fill: "#000000"});
    		button15.addChild(text);
        this.xpos+=38;
				
    }

}

export default QuizType;
